<template>
  <div class="u-content">
    <h2><i class="fa fa-box"></i> | {{ i18n.$t("Update") }}</h2>
    <div id="updatePage">
      <a :href="updatePackageAddr" class="update">
        <i class="fa fa-box"></i>&nbsp; {{ i18n.$t("UpdatePackage") }}
      </a>

      <a :href="updateFirmwareAddr" class="update">
        <i class="fa fa-microchip"></i>&nbsp; {{ i18n.$t("UpdateFirmware") }}
      </a>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from "vue";
import I18nPlugin from "@/plugins/i18nPlugin";
export default defineComponent({
  name: "Addons",
  setup() {
    const i18n = I18nPlugin.use();
    const address =
      process.env.NODE_ENV == "development"
        ? process.env.VUE_APP_DEVICE_ADDRESS
        : window.location.host;

    const updateFirmwareAddr = ref("");
    const updatePackageAddr = ref("");
    if (
      address &&
      address.indexOf(":") < 0 &&
      window.location.protocol === "http:"
    ) {
      updateFirmwareAddr.value = "http://" + address + ":8080/update_firmware";
      updatePackageAddr.value = "http://" + address + ":8080";
    }

    return { i18n, updateFirmwareAddr, updatePackageAddr };
  }
});
</script>

<style lang="scss" scoped>
#updatePage {
  padding: 2ex;
  .update {
    display: block;
    color: #333333;
    height: 6ex;
    width: 100%;
    border-radius: 1ex;
    background: #aaaaaa;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 1ex;
  }
}
</style>
